import React from 'react';
import { Link } from 'react-router-dom';

import "../cssfiles/Home.css";
import Faq from './Faq';
import Footer from './Footer';
import Getaccount from './Getaccount';
import abouima from '../../assets/images/5.png';
import servimage from '../../assets/images/1.png'
import Sercont from './Sercont';

function Home() {
    return (
        <div>
            <div className="home__first">
                <div className="home__first__content">
                    <div className="home__first__text1"><span style={{color:'#18C772'}}>Accounting</span> is the language of Business</div>
                    <div className="home__first__text2">Let us manage your books so that you can manage your business well. You need not be an expert with Books. Let us handle it.</div>
                    <div className="home__first__but__cont">
                        <div style={{width:'1px'}}></div>
                        <Link to='/contact' style={{ textDecoration: 'none' }}><div className="home__first__but">Get Started</div></Link>
                    </div>
                </div>
                <div className="home__first__empty" />
            </div>
            <div className="home__second">
                <div className='home__second__about'>
                    <p className='home__second__about__head'>About <span style={{color: '#18C772', fontSize:'40px'}}>Compters</span></p>
                    <p className='home__second__about__detail'>We are a Bookkeeping firm who can help you manage a very important of your business. Bookkeeping is the part where various business transactions get recorded and are summarised in the  Profit/Loss and Balance Sheet and it is important for business owners to realise the need for this for making important business decisions in the future apart from meeting timely legal needs...</p>
                    <div style={{display:'flex', justifyContent: 'space-between'}}>
                        <div style={{width: '1px', height: '1px'}}></div>
                        <Link to='/about' style={{ textDecoration: 'none' }}><div className='home__second__aboutbut'>Read More</div></Link>
                    </div>
                </div>
                <div className='home__second__aboutimag'>
                    <img src={abouima} className='home__second__ab__img' alt='something is wrong' />
                </div>
            </div>
            <div className="home__slogon">Focus On Growing Your Business Trust The <span className="home__slogon__span">Accounting</span> To Us</div>
            <div className="home__second" style={{backgroundColor:'#F4F4F4'}}>
                <div className='home__second__aboutimag'>
                    <img src={servimage} className='home__second__ab__img' alt='something is wrong' />
                </div>
                <div className='home__second__about'>
                    <p className='home__second__about__head'>At Your <span style={{color: '#18C772', fontSize:'40px'}}>Service</span></p>
                    <p className='home__second__about__detail'>Compters is a full-service accounting firm that can meet all your diverse and unique accounting needs. Our highly trained professionals will give you the quality service and expertise that you deserve.</p>
                    <div style={{display:'flex', justifyContent: 'space-between'}}>
                        <div style={{width: '1px', height: '1px'}}></div>
                        <Link to='/about' style={{ textDecoration: 'none' }}><div className='home__second__aboutbut'>Read Services</div></Link>
                    </div>
                </div>
            </div>
            <div style={{height:'70px'}} />
            <div style={{padding:'0px 05vw'}}><Sercont /></div>
            <div style={{height:'70px'}} />
            <Faq />
            <Getaccount />
            <Footer />
        </div>
    )
}

export default Home;
